* {
    outline: none;
}

.header {
    font-size: clamp(5vh, 2em, 5vw) !important;
    text-align: center;
}
a {
    text-decoration: none !important;
}

a:hover {
    filter: contrast(200%);
    transition: all 0.5s ease;
}

.chart {
    margin: auto;
    width: 75vw;
    overflow: hidden;
}

button {
    margin: 1vh auto !important;
}

input[type="checkbox"],
label {
    cursor: pointer;
}

.loading {
    overflow: hidden;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
